@import 'https://fonts.googleapis.com/css?family=Roboto:400,700';
[role="button"],
input[type="submit"],
input[type="reset"],
input[type="button"],
button {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

input[type="submit"],
input[type="reset"],
input[type="button"],
button {
  background: none;
  border: 0;
  border-radius: 0px;
  color: inherit;
  cursor: pointer;
  font: inherit;
  line-height: normal;
  overflow: visible;
  padding: 0;
  -webkit-appearance: button;
  /* for input */
  -webkit-user-select: none;
  /* for button */
  -moz-user-select: none;
  -ms-user-select: none; }

input::-moz-focus-inner,
button::-moz-focus-inner {
  border: 0;
  padding: 0; }

[role="button"] {
  color: inherit;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  white-space: pre;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none; }

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

button, button:hover, button:focus, button:active {
  outline: 0; }

a, a:hover, a:focus, a:active {
  color: inherit; }

* {
  box-sizing: border-box; }

body {
  font-family: 'Roboto Condensed', Helvetica, Arial, serif;
  font-size: 1em;
  line-height: 1.5; }

p {
  margin-bottom: 1em; }

h1 {
  font-size: 5em;
  line-height: 1em;
  margin-bottom: 0.5em; }
  @media (max-width: 1199px) {
    h1 {
      font-size: 4em; } }
  @media (max-width: 991px) {
    h1 {
      font-size: 3.5em; } }
  @media (max-width: 767px) {
    h1 {
      font-size: 3em; } }

h2 {
  font-size: 3em;
  line-height: 1em;
  margin-bottom: 0.5em; }
  @media (max-width: 1199px) {
    h2 {
      font-size: 2.4em; } }
  @media (max-width: 991px) {
    h2 {
      font-size: 2.1em; } }
  @media (max-width: 767px) {
    h2 {
      font-size: 1.8em; } }

h3 {
  font-size: 2em;
  line-height: 1em;
  margin-bottom: 0.5em; }
  @media (max-width: 1199px) {
    h3 {
      font-size: 1.6em; } }
  @media (max-width: 991px) {
    h3 {
      font-size: 1.4em; } }
  @media (max-width: 767px) {
    h3 {
      font-size: 1.2em; } }

h4 {
  font-size: 1.5em;
  line-height: 1em;
  margin-bottom: 0.5em; }
  @media (max-width: 1199px) {
    h4 {
      font-size: 1.2em; } }
  @media (max-width: 991px) {
    h4 {
      font-size: 1.05em; } }
  @media (max-width: 767px) {
    h4 {
      font-size: 0.9em; } }

.btn {
  background-color: #E70D3B;
  color: #fff;
  height: 50px;
  width: 230px;
  line-height: 50px;
  border-radius: 25px;
  display: inline-block;
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  border: 2px solid transparent; }
  .btn:hover {
    color: #fff;
    background-color: #FF345F;
    border-color: #E70D3B; }

section {
  padding: 7em 0em;
  position: relative; }
  section:after {
    position: absolute;
    bottom: 0px;
    left: 0px;
    content: '';
    display: block;
    width: 100%;
    height: 1px;
    background-image: radial-gradient(circle, rgba(0, 0, 0, 0.1), transparent); }
  section.banner {
    padding: 0em;
    height: 960px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #fff;
    background-position: center;
    background-size: cover; }
    @media (max-height: 1199px) {
      section.banner {
        height: 800px; } }
    @media (max-height: 991px) {
      section.banner {
        height: 580px; } }
    @media (max-height: 767px) {
      section.banner {
        height: 375px; } }
    section.banner .container {
      position: relative;
      padding-right: 20em; }
      @media (max-width: 1199px) {
        section.banner .container {
          padding-right: 20em; } }
      @media (max-width: 991px) {
        section.banner .container {
          padding-right: 10em; } }
      @media (max-width: 767px) {
        section.banner .container {
          padding-right: 5em; } }
  section.rich-text-section .container {
    max-width: 600px; }
    section.rich-text-section .container img {
      max-width: 100%; }
  section.buttons .container {
    text-align: center; }
    section.buttons .container .btn {
      margin: 1em; }
  section.images .container {
    text-align: center; }
    section.images .container img {
      margin: 2em 1em 0em 1em;
      line-height: 1em;
      height: 3em; }
  section .container {
    position: relative; }

.container {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 960px; }
  @media (max-width: 1199px) {
    .container {
      max-width: 800px; } }
  @media (max-width: 991px) {
    .container {
      max-width: 580px; } }
  @media (max-width: 767px) {
    .container {
      max-width: calc(100% - 20px); } }

footer {
  background-color: #222222;
  color: #fff;
  padding: 4em 0em;
  text-align: center; }
